.photos {
  display: flex;
  justify-content: space-between;
  // padding: 0 120px;
  
  &__img {
    width: calc(50% - 10px);
    height: 256px;
    object-fit: cover;
    object-position: center;
  }

  &__wider {
    width: calc(67% - 10px);
  }

  &__narrower {
    width: calc(33% - 10px);
  }
}
